*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

ul {
  list-style: none;
}

input {
  border: none;
  outline: none;
}

button {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  outline: none;
}

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
}

html {
  scroll-behavior: smooth;
}

body {
  color: #000;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 400;
}

.container {
  min-width: 320px;
  max-width: 1400px;
  margin: 0 auto;
}

img {
  width: 100%;
  max-width: 100%;
  display: block;
}

.button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background: #2a3246;
  border-radius: 5px;
  transition: transform .25s ease-in-out;
}

.button:hover {
  transform: scale(1.1);
}

.logo {
  color: #000;
  color: #000;
}

.logo--active {
  color: #8f95a5;
}

.header__logo {
  z-index: 19000;
  font-size: 24px;
  transition: color .25s ease-in-out;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 40px 15px 29px;
  display: flex;
}

.logo:hover {
  color: #8f95a5;
}

.logo:hover .logo--active {
  color: #000;
}

.nav__button {
  color: #263238;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #263238;
  padding: 16px 65px;
  font-size: 20px;
  transition: transform .25s ease-in-out;
}

.nav__button:hover {
  transform: scale(1.1);
}

.nav-logo__container {
  z-index: 999;
}

.to-top__button {
  z-index: 999;
  opacity: 0;
  border-radius: 50%;
  padding: 20px;
  transition: opacity .3s;
  animation: scrollAnimation 3s cubic-bezier(.4, 0, .2, 1) infinite;
  position: fixed;
  bottom: 30px;
  right: 30px;
}

.to-top__button:hover {
  animation-play-state: paused;
}

.to-top__button.scrolled {
  opacity: 1;
}

.to-top__svg {
  width: 30px;
  height: 30px;
  fill: #fff;
}

@keyframes scrollAnimation {
  0% {
    transform: translateY(0);
  }

  20% {
    transform: translateY(-50px);
  }

  40% {
    transform: translateY(-25px);
  }

  60% {
    transform: translateY(-50px);
  }
}

.nav__link:after {
  content: "";
  height: 2px;
  width: 0;
  background-color: #000;
  transition: width .25s ease-in-out;
  display: block;
}

.nav__item {
  transition: transform .25s ease-in-out;
}

.nav__item:hover {
  transform: scale(1.1);
}

.nav__link:hover:after {
  width: 100%;
}

